*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

header{
  color: rgb(255, 255, 255);
  background-color: rgb(8, 19, 78);
  padding: 15px;
  text-align: center;
  position: fixed;
  width: 100%;
  font-size: larger;

  z-index: 1;
}

.addForm label{
  position: absolute;
  z-index: -9999;
}

.contented ul .contentLabel{
  position: absolute;
  right: 30%;
}

.addForm input{
  height: 30px;
  min-width: calc(100% - 30px);
  text-align: center;
  padding: 10px;
  width: 100%;
  position: fixed;
  top: 50px;
  left: 0;
  z-index: 1;
}

.addForm svg{
  font-size: 20px;
  outline: none;
  position: fixed;
  right: 15px;
  top: 55px;
  z-index: 1;
}
.addForm svg:hover{
  color: rgb(21, 255, 0);
  background-color: rgb(0, 0, 0);
  outline: none;

}
.addForm{
  align-items: center;
  width: 100%;
  display: flex;
  padding: 10px;
  justify-content: space-between;
}

.contented{
  color: rgb(27, 26, 26);
  font-size: large;
  width: 100%;
  text-align: center;
  position: absolute;
}

.contented ul{
  list-style-type: none;
  width: 100%;
  text-align: center;
  position: absolute;
  top: 10%;
  padding-top: 40px;
  padding-bottom: 40px;
}

.contented ul li:hover{
  background-color: rgb(14, 197, 24);
  list-style-type: none;
}

.contented ul li{
  color: rgb(0, 0, 0);
  background-color: aquamarine;
  padding: 5px;
  margin: 5px;
  height: 29px;
  text-align: center;
}

.contented ul li input[type="checkbox"]{
  width: 20px;
  height: 20px;
  left: 20px;
  position: absolute;
}


.contented svg{
  position: absolute;
  right: 20px;
  color: rgb(83, 55, 105);
}

.contented svg:hover {
  color: rgb(255, 0, 0);
}


Footer{
  color: rgb(255, 255, 255);
  background-color: rgb(8, 19, 78);
  padding: 15px;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  
}
.empty{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: larger;
  font-weight: bolder;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}


.logo{

  position: absolute;
  z-index: 6;
  background-color: rgb(0, 0, 0);
  color: aliceblue;
  height: 100%;
  width: 100%;
}

.logo p{

  display: flex;
  position: absolute;
  font-size: 60px;
  top: 50%;
  left: 40%;
  translate:-50% -50%;

}

.logo2  {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: black;
  height: 100%;
  text-align: center;
  width: 100%;
  z-index: 5;


}



.logo2 p img{

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: black;
  width: 200px;

  
  animation-name: rotateView;
  animation-duration:8s;
  animation-fill-mode: both;

}



@keyframes rotateView {
  
  0%{
left: -50%;
  }
  50%{

    left: -45%;
    transform: rotate(-360deg);
    top: 50%;
  }

  100%{
    top: 50%;
    position: absolute;
    left: 25%;
    transform: rotate(0deg);
  }
}


.dot{
  display: flex;
}



.dot1 , .dot2 ,.dot3{
  position: absolute;
  left: 100%;
}




.dot1{
  animation-name: dotview;
  animation-duration: 1.5s;
  animation-fill-mode: both;
}

@keyframes dotview {
  
  from{

    transform: translateX(0px);
  }
  to{
    
    transform: translateX(20px);

  }
}



.dot2{
  animation-name: dotview2;
  animation-duration: 3s;
  animation-fill-mode: both;
}

@keyframes dotview2 {
  
  0%{

    transform: translateX(0px);
  }

  50%{
    transform: translateX(20px);
  }

  100%{
    
    transform: translateX(40px);

  }
}

.dot3{
  animation-name: dotview3;
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-delay: 2s;
}

@keyframes dotview3 {
  
  from{

    transform: translateX(0px);
  }
  to{
    
    transform: translateX(0px);

  }
}